<template>
    <div class="detailsPage">
        <!-- 导航栏 -->
        <van-nav-bar class="myBars" :border="false">
            <template #left>
                <van-icon name="cross" class="myCross" @click="getBack"/>
            </template>
            <template #right>
                <van-icon name="share-o" class="myShare"  @click="showShare = true"/>
                <van-icon name="like-o" class="myLike" @click="goPage('Collect')"/>
            </template>
        </van-nav-bar>
       
        <!-- 商品图片 -->
        <van-image :src="articles.large_img" class="myImgs">
             <template v-slot:error>加载失败</template>
        </van-image>
        
        <!-- 商品名称 -->
        <div class="goodName">
            <p>{{articles.name}}</p>
            <p>{{articles.enname}}</p>
        </div>
        
        <!-- 商品配置 -->
        <Allocation :Alls="articles" @gai="ff_gai($event)"></Allocation>
        

        <!-- 推荐区 -->
        <div class="recoms">
            <p>超值加购推荐,满足加倍~</p>
            <div>
                <a >
                     <van-card
                    v-for="(item,index) in recomeData"
                    :key="index"
                    tag="限定专享"
                    :price="item.price"
                    :desc="item.enname"
                    :title="item.name"
                    :thumb="item.largeImg"
                    :origin-price="item.price-0+20"
                    class="rec"
                    @click="goPage('Details',item.pid)"
                 />

                </a>
               
            </div>
        </div>

        <!-- 商品详情 -->
        <div class="circum">
            <p class="particulars">商品详情</p>
            <p v-for="(item,index) in  articles.descArr" :key="index">
                {{item}}
            </p>
        </div>

       

        <!-- 单品订单预览 -->
        <div class="preview">
            <div class="careful">
                <p>
                    <span>￥{{(articles.price-0) * (articles.abbr-0)}}</span>
                    <span>￥{{ (articles.price-0+20) * (articles.abbr-0)}}</span>
               </p>
               <p ref="rules">
                   {{articles.name}}￥{{ (articles.price-0) * (articles.abbr-0) }}{{ [articles.creamFalse , articles.creamTitle ] | ff_huang}}{{ [articles.sugarFalse , articles.sugarTitle ] | ff_huang}}{{[articles.temFalse , articles.temTitle] |  ff_huang}}
               </p>
            </div>
            <van-stepper class="step" v-model="articles.abbr" theme="round" button-size="22" disable-input />
        </div>

        <!-- 商品确认 -->
        <van-goods-action>
                <van-goods-action-icon  icon="shopping-cart" :badge="cours==0 ? '' : cours" color="#999999" text="购物车"   @click="goPage('Cat')" >
                    
                </van-goods-action-icon>
                <van-goods-action-icon  :text="Like ? '已收藏' : '未收藏'"  icon="star"  :color="Like ? '#ef5721'  : '#999999'" @click="onClickIcon" />
                <van-goods-action-button
                    class="buy01"
                    text="立即购买"
                    @click="goPath('Pay')"
                />
                <van-goods-action-button
                    class="buy02"
                    text="加入购物车"
                    @click="onClickButton"
                />
        </van-goods-action>

        <!-- 分享 -->
        <van-share-sheet
        v-model="showShare"
        title="立即分享给好友"
        :options="options"
        @select="onSelect"
        />

    </div>
</template>

<script>
    import { Toast } from 'vant';
    import Allocation from '../components/Allocation.vue'
    import '../assets/less/Details.less'
    // import req  from '../assets/js/myaxios.js'

    export default {
        name:'Details',
        data() {
            return {
                articles   : {},
                recomeData : [],
                Like       : false,
                cours      : 0,
                strRule    : '' ,
                isNum      : 1,
                showShare  : false,
                options    : [
                    { name : '微信', icon: 'wechat' },
                    { name : '微博', icon: 'weibo' },
                    { name : '复制链接', icon: 'link' },
                    { name : '分享海报', icon: 'poster' },
                    { name : '二维码', icon: 'qrcode' },
                ], 
                sid        :  '',
            }
        },
         methods: {
            //  点击收藏
            onClickIcon() {
                // let userId = localStorage.getItem('userId');
                // 
                 
                if(this.useId){
                    this.Like = !this.Like;
                    this.ff_testLogin();

                }else{
                        this.$toast('请先登录');
                        setTimeout(()=>{
                            this.$router.push('Login');
                        },500)
                }
               
            },
            // 点击加入购物车
            onClickButton() {
              
               if(this.useId){

                        this.isNum++;
                        if(this.isNum==2){
                               this.ff_addShopcart();

                        }else{
                                this.$toast('已经加入购物车');
                    }
                   
               }else{
                    this.$toast('请先登录');
                    setTimeout(()=>{
                        this.$router.push('Login');
                    },500)
                }
               
               
                
                
            },
            // 配置的选项
            ff_gai(e){
              
                this.articles[e.name] = !e.isFalse;   
            },
            // 返回上一级
            getBack(){
                 this.$router.go(-1);
            },
            // 跳转路由
            goPage(name,pid=null){
                this.$router.push({name,query:{pid}});
                setTimeout(()=>{
                      this.ff_Dates();
                },300)
                
            },

            // 商品的详情请求
            ff_Dates(){
                        let pid  = this.$route.query.pid;
                        
                        // 商品的详情请求
                        this.axios({
                            url:'/productDetail',
                            method:'GET',
                            params:{
                                appkey:this.appkey,
                                pid : pid
                            }
                        })
                        .then(res=>{
                            let a = res.data.result[0];
                            
                    
                            a.abbr = 1;
                            
                            a.isCream = false;
                            a.isSugar = false;
                            a.isTem   = false;

                            a.creamFalse  = true;
                            a.sugarFalse  = true;
                            a.temFalse    = true;

                            this.articles = a;

                        if(this.articles.cream){
                                this.articles.isCream = true;
                                this.articles.creamTitle = this.articles.cream.split('/');
                        }
                        if(this.articles.sugar){
                                this.articles.isSugar = true;
                                this.articles.sugarTitle = this.articles.sugar.split('/');
                        }
                        if(this.articles.tem){
                                this.articles.isTem = true;
                                this.articles.temTitle = this.articles.tem.split('/');
                        }


                        let Str    = this.articles.desc;
                        this.articles.descArr = Str.split('。');
                        //  

                        })
                        .catch(err=>{
                             
                        });
            },

            // 判断收藏与否
            ff_findLike(){
                let pid = this.$route.query.pid;
                  //发起查询收藏商品请求
                this.axios({
                        method: 'GET',
                        url: '/findlike',
                        params: {
                            appkey: this.appkey,
                            tokenString :this.useId,
                            pid
                        }
                        })
                        .then(res => {
                            this.$toast.clear();
                            if (res.data.code == 1000) {
                                if (res.data.result.length > 0) {
                                    //收藏商品成功
                                    this.Like = true;
                                }
                            }
                        })
                        .catch(err => {
                        this.$toast.clear();
                         
                })

            },

            // 收藏的数据请求与逻辑
            ff_testLogin(){
                let url = this.Like ? '/like' : '/notlike';
                // 点击收藏与删除收藏
                if(this.useId){
                     this.axios({
                         url,
                         method:'POST',
                         data:{ 
                            appkey: this.appkey,
                            tokenString: this.useId,
                            pid : this.articles.pid
                         }
                     })
                     .then(res=>{
                          
                          if(res.data.code == 800){
                               this.Like = true
                          }else if(res.data.code == 901){
                               this.Like = false
                          }
                     })
                     .catch(err=>{
                           
                     })
                     
                }else{
                    this.$toast('请先登录');
                    setTimeout(()=>{
                        this.$router.push('Login');
                    },500)
                }
                 
            },
            
            // 查询购物车的商品数量
            ff_shoppingCarts(){
                    this.axios({
                                url:'/findAllShopcart',
                                method:'GET',
                                params:{
                                    appkey      : this.appkey,
                                    tokenString : this.useId
                                }
                                })
                                .then(res=>{
                                    
                                    if(res.data.code == 5000){
                                        this.cours = res.data.result.length;
                                        
                                     
                                    }
                                })
                                .catch(err=>{
                                     
                                })
            },

            // 加入购物车的请求
            ff_addShopcart(){


                //加工配置
                    let Arr01 = this.$refs.rules.innerText.split('+');
                    let strRule =  '';
                    Arr01.forEach((item,index)=>{
                        if(index > 0){
                                strRule += `${item}`;
                        }
                    });
                    let arrRule = strRule.split('￥0');
                    let strRules =  '';
                    arrRule.forEach((item,index)=>{
                        if(index < arrRule.length-1){
                                strRules += `${item}/`;
                        }
                    });
                    strRules = strRules.slice(0,-1);
                        
                        this.axios({
                            url:'/addShopcart',
                            method:'POST',
                            data:{
                                pid         : this.articles.pid,
                                count       : this.articles.abbr,
                                rule        : strRules,
                                appkey      : this.appkey,
                                tokenString : this.useId,
                            }
                        })
                        .then(res=>{
                            
                            if(res.data.code == 3000){
                                        this.sid = res.data.sid;
                                        
                                        this.$toast('成功加入购物车');
                                        this.ff_shoppingCarts();
                                
                                
                            }
                        })
                        .catch(err=>{
                                
                        });

                    
            },

            //  分享逻辑
            onSelect(option) {
                Toast.loading({
                    message: `       ${option.name}        维护中`,
                    forbidClick: true,
                    loadingType: 'spinner',
                });
                this.showShare = false;
            },

            // 跳转支付模块
            goPath(name){
                
                
                  
                    this.ff_addShopcart();
                    
                    setTimeout(()=>{
                        
                            let sid  = [];
                            sid.push(this.sid) ;
                            let sids = JSON.stringify(sid);
                            this.$router.push({name,query:{sids}})

                    },1500)
                   
                    
                
                
                
                
                
                
                
            }
            
        },
        // 初始化数据
        created() {
             this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
            });
             
             this.ff_Dates();
            
            // 推荐商品
             this.axios({
                url:'/typeProducts',
                method:'GET',
                params:{
                    appkey:this.appkey,
                    key:'isHot',
                    value:1,
                }
            })
            .then(res=>{
                res.data.result.forEach((item,index)=>{
                    if(index>3){
                       
                         this.recomeData.push(item);  
                          
                    }
                });
            })
            .catch(err=>{
                 
            });


            // 一开始就判断商品是否收藏
            setTimeout(()=>{
                  this.ff_findLike();
            },500)
            
            // 查询购物车的商品数量
            this.ff_shoppingCarts();
        },
        // 过滤器
        filters:{
           ff_huang(v){
               let Strs =  '';
               if(v[1]){
                   Strs = v[0] ? v[1][0] :  v[1][1];
                   let text = `+${Strs}￥0`
            
                   return text;
               }else{
                   return Strs;
               }
           }
        },
        components:{
            Allocation
        }
    }
</script>

<style lang="less" scoped>

</style>