<template>
    <div class="myAllocation">
         <div v-if="Alls.isCream">
             <span>{{Alls.cream_desc}}</span>
             <span @click="ff_gai('creamFalse',Alls.creamFalse)" :class="{'attr' : Alls.creamFalse}">{{Alls.creamTitle[0]}}</span>
             <span @click="ff_gai('creamFalse',Alls.creamFalse)" :class="{'attr' : !Alls.creamFalse}">{{Alls.creamTitle[1]}}</span>
         </div>
         <div v-if="Alls.isSugar">
             <span>{{Alls.sugar_desc}}</span>
             <span @click="ff_gai('sugarFalse', Alls.sugarFalse)"  :class="{'attr' : Alls.sugarFalse}">{{Alls.sugarTitle[0]}}</span>
             <span @click="ff_gai('sugarFalse', Alls.sugarFalse)"  :class="{'attr' : !Alls.sugarFalse}">{{Alls.sugarTitle[1]}}</span>
         </div>
         <div v-if="Alls.isTem">
             <span>{{Alls.tem_desc}}</span>
             <span @click="ff_gai('temFalse', Alls.temFalse)" :class="{'attr' : Alls.temFalse}">{{Alls.temTitle[0]}}</span>
             <span @click="ff_gai('temFalse', Alls.temFalse)" :class="{'attr' : !Alls.temFalse}" v-if="Alls.temTitle[1]">{{Alls.temTitle[1]}}</span>
         </div>
    </div>
</template>

<script>
    export default {
        name:'Allocation',
        props:['Alls'],        
        methods: {
            ff_gai(name,isFalse){
                 this.$emit('gai',{name,isFalse} );
            }
        },
    }
</script>

<style lang="less" scoped>
     .myAllocation{
         width: 97%;
         background-color: #fff;
         border-radius: 7px;
         margin: 8px auto;

         div{
             overflow: hidden;
             padding: 10px 10px 8px;
             text-align: left;

             span{
                 display: inline-block;
                 font-size: 13px;
                 border-radius: 20px;
                 width: 70px;
                 height: 25px;
                 line-height: 25px;
                 text-align: center;
                 font-weight: 540;
                 background-color: #f5f5f5;
                 margin-left: 10px;
                
             }
             >span:nth-child(1){
                 color: #000;
                 font-size: 13px;
                 text-align: left;
                 width: 40px;
                 background-color: #fff;
                 font-weight: 545;

                
             }
              .attr{
                  color: #6983bf !important;
                  border: #6983bf 1px solid !important;
                  background-color: #f5f8ff !important;
              }
             
             
         }
     }
</style>